@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@600&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  color: rgba(0, 0, 0, 0.85);
  font-size: 1.5rem;
}

body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0 5%;
}


@-webkit-keyframes fade-in-up {
  from {
    -webkit-transform: translateY(45%);
            transform: translateY(45%);
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in-up {
  from {
    -webkit-transform: translateY(45%);
            transform: translateY(45%);
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fade-in-down {
  from {
    -webkit-transform: translateY(-45%);
            transform: translateY(-45%);
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in-down {
  from {
    -webkit-transform: translateY(-45%);
            transform: translateY(-45%);
  }
  to {
    opacity: 1;
  }
}

header * {
  margin: 0.5% 0;
}

header {
  margin-left: 10%;
  margin-top: 1.5%;
  height: 85vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

header p span {
  color: #ed5940;
}

header h1 {
  font-size: 2em;
  font-family: "Source Code Pro", monospace;
}

header h1 span {
  font-size: 1em;
}

#description {
  color: rgba(0, 0, 0, 0.45);
  margin-top: 1.5%;
  opacity: 0;
  -webkit-animation: fade-in-down 1s;
          animation: fade-in-down 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1.7s;
          animation-delay: 1.7s;
}

a:not(.profile) {
  margin-top: 6%;
  font-size: 0.65em;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 15px 50px;
  text-decoration: none;
  border: 2px solid transparent;
  border-radius: 50px;
  background-color: #ed5940;
  color: white;
  text-align: center;
}

a:hover:not(.profile) {
  background-color: transparent;
  color: #ed5940;
  border: 2px solid #ed5940;
}

.buttonAnimation {
  opacity: 0;
  -webkit-animation: fade-in-up 1s;
          animation: fade-in-up 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 2.5s;
          animation-delay: 2.5s;
}

#name {
  opacity: 0;
  -webkit-animation: fade-in-up 1s;
          animation: fade-in-up 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 0.9s;
          animation-delay: 0.9s;
}

section {
  margin-bottom: 5% !important;
  border-radius: 15px;
}

.innerSectionMargin {
  margin: 0 6.5% 0 10%;
  padding: 5% 0;
}

.sectionTitle {
  margin-top: 1%;
  margin-bottom: 3%;
  text-transform: capitalize;
}

.expArticle {
  margin-bottom: 2%;
}

.expArticle *:not(ul) {
  font-size: 0.45em;
}

.expArticle p:first-child {
  font-size: 0.65em;
}

.role {
  color: rgba(0, 0, 0, 0.45);
  margin-bottom: 2%;
  margin-top: 0.5%;
}

ul {
  line-height: 0.85em;
  margin-bottom: 6.5%;
}

.Collapsible span {
  cursor: pointer;
  font-size: 0.45em;
  color: rgba(0, 0, 0, 0.45);
}

.Collapsible span:hover {
  text-decoration: underline;
}

ul {
  margin-left: 1em;
}

.skills {
  margin: 0 10%;
  margin-top: 10%;
}

.skills h1 {
  font-size: 1.2em;
}

#sectionDescription {
  color: rgba(0, 0, 0, 0.45);
  font-size: 0.65em;
  margin-top: 1%;
  margin-bottom: 3%;
}

.skillArticle {
  margin-bottom: 2%;
}

.skillArticle p:first-child {
  font-size: 0.75em;
  font-weight: 500;
}

.description {
  font-size: 0.45em;
  font-weight: 500;
  margin-top: 6.5%;
}

figure {
  width: 150px;
  height: 165px;
  border-radius: 30px;
  margin-top: 2.1%;
  margin-right: 2.1%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

figure img {
  width: 50%;
  height: 50%;
}

.images {
  display: flex;
  flex-wrap: wrap;
}

footer {
  margin: 0 10%;
}

.actualSection {
  background-color: #fbf1f4;
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}

#faq {
  position: relative;
  z-index: 1;
  top: 11px;
  left: 5%;
  width: 150px;
}

#blob {
  position: absolute;
  z-index: 3;
  width: 85%;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  right: -85px;
  top: -100px;
}

footer a:not(.profile) {
  margin: 0;
  margin-right: 9%;
  z-index: 4;
}

.actualSection article {
  margin: 5% 0;
  margin-left: 9%;
  z-index: 4;
}

.actualSection p {
  color: rgba(0, 0, 0, 0.45);
  font-size: 0.65em;
  margin-top: 4%;
}

.actualSection h1 {
  font-size: 1.2em;
}

.contactInfo {
  margin-top: 10%;
  text-align: center;
}

.contactInfo * {
  font-size: 0.65em;
  margin: 6px;
}

.contactInfo p:first-child {
  font-weight: 600;
}

.contactInfo p:nth-child(2) {
  color: rgba(0, 0, 0, 0.45);
  font-size: 0.45em;
  margin-top: 3%;
}

.contactInfo p:nth-child(4) {
  font-size: 0.45em;
  margin-top: 8.5px;
}

.contactInfo img {
  width: 22px;
  height: 22px;
}

footer figure {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  padding: 10px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

#instagram {
  background-color: rgba(152, 74, 195, 0.1);
}

#instagram:hover {
  box-shadow: 0 0 10px 5px rgba(152, 74, 195, 0.25);
}

#linkedin {
  background-color: rgba(0, 118, 181, 0.1);
}

#linkedin:hover {
  box-shadow: 0 0 10px 5px rgba(0, 118, 181, 0.25);
}

#github {
  margin-top: 3%;
  background-color: rgba(234, 76, 137, 0.1);
}

#github:hover {
  box-shadow: 0 0 10px 5px rgba(234, 76, 137, 0.25);
}

